@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@200&display=swap'); */

*{
    font-family: 'Cairo', sans-serif;
    box-sizing: border-box;
}



body{
    background-color: #f3f3f3;
    /* background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%); */
}

.app-main{
    margin : 1% auto; 
    padding: 1%;
    width: 50%;
    min-height: 50vh;
    background-color: white;
    box-shadow: -6px 10px 12px -2px rgba(0,0,0,0.48);
}
.transaction_body{
    width: 100%;
    padding: 10px;
}


.transaction_body-input{
    text-align: center;
    padding: 1%;
}

.transaction_body-input-button{
    width: 100%;
    text-align: center;
    margin-top: 1%;
    padding: 10px;
}

.transaction_body-input-button-itself{
    border: none;
    width: 100%;
    padding: 3%;
    background-color: rgb(117, 115, 250);
}

.transaction_body-input-button-itself:hover {
   
    color : white;
    cursor:pointer;
    background-color: rgb(54, 158, 255);
}
.hide_history_button{
    display: none;
}

.show_history_button{
    display: inline-block;

}

.btn_effect{
    border: none;
    width: 100%;
    padding: 3px;
    color : white;
    background-color: rgb(255, 103, 103);
}

.btn_effect:hover{
    border: none;
    width: 100%;
    padding: 10px;
    color : white;
    cursor:pointer;
    background-color: rgb(255, 0, 0);
}

.ul_list{
    list-style-type: none;
    width: 100%;
    padding: 0;
}

.Positive_History_list{
    margin: 3% auto;
    padding: 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-right: 5px solid green;
    background-color: rgb(243, 243, 243);
    box-shadow: 0px 8px 4px -5px rgba(0,0,0,0.68);
}

.Negative_History_list{
    margin: 3% auto;
    padding: 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-right: 5px solid red;
    background-color: rgb(243, 243, 243);
    box-shadow: 0px 8px 4px -5px rgba(0,0,0,0.68);
}


.History_list-li{
    cursor:pointer;
}


.Balance_detail_container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.income{
    width: 100%;
    margin:1%;
    color: rgb(94, 255, 0);
    padding: 0;
    display: flex;
    flex-direction: column;
}
.income_head{
    background-color: green;
    border-radius: 10px; 
    color: white;
    text-align: center;
    font-family: 'Cairo', sans-serif;
}

.income_body{
    color: green;
    text-align: center;
    font-family: 'Cairo', sans-serif;
    border-bottom: 3px solid green;
    border-radius: 10px; 
}

.cost{
    width: 100%;
    margin:1%;
    color: rgb(94, 255, 0);
    padding: 0;
    display: flex;
    flex-direction: column;
}

.cost_head{
    background-color: rgb(233, 137, 11);
    border-radius: 10px; 
    color: white;
    text-align: center;
    font-family: 'Cairo', sans-serif;
}

.cost_body{
    color: rgb(233, 137, 11);
    text-align: center;
    font-family: 'Cairo', sans-serif;
    border-bottom: 3px solid  rgb(233, 137, 11);
    border-radius: 10px; 
}


.header_container_p{
    text-align: center;
    color : green
}

.header_container_n{
    text-align: center;
    color : rgb(233, 137, 11);
}

